$dropzone--background: #e9eff5;
$dropzone--foreground: #53718a;
$dropzone--foreground--hover: darken($dropzone--foreground, 20%);
$dropzone--foreground--dragging: $blue;
$dropzone--border: #d6e2ea;
$dropzone--border--hover: darken($dropzone--border, 20%);
$dropzone--border--dragging: $blue;
$dropzone--browse--foreground: $blue;
$dropzone--icon--fill: #c7d3df;
$dropzone--icon--fill--hover: darken($dropzone--icon--fill, 15%);
$dropzone--icon--fill--dragging: $blue;
$dropzone--file--icon--fill: #adbfce;

.dropzone {
  align-items: center;
  background: $dropzone--background;
  border: 1px dashed $dropzone--border;
  border-radius: 4px;
  color: $dropzone--foreground;
  display: flex;
  flex-direction: column;
  text-align: center;
  transition: border 0.25s, color 0.25s;
  width: 100%;

  &:hover {
    border-color: $dropzone--border--hover;

    .dropzone__icon {

      .icon {
        fill: $dropzone--icon--fill--hover;
      }
    }
  }

  &--is-dragging {

    &,
    &:hover {
      border-color: $dropzone--border--dragging;
      color: $dropzone--foreground--dragging;

      .dropzone__icon {

        .icon {
          fill: $dropzone--icon--fill--dragging;
        }
      }
    }
  }

  &__icon {

    .icon {

      &--files {
        fill: $dropzone--icon--fill;
        height: 64px;
        transition: fill 0.25s;
        width: 64px;

        &__file {

          &--right,
          &--left {
            fill-opacity: 0.5;
          }
        }
      }
    }
  }

  &__copy {
    cursor: pointer;
    flex: 1;
    font-size: 0.85em;
    padding: $spacing-unit;
    width: 100%;
  }

  &__browse-button {
    color: $dropzone--browse--foreground;
    text-decoration: underline;
  }

  &__selected-files {
    @extend .textbox;
    @extend .textbox.is-fulfilled;
    border-radius: 4px 4px 0 0 ;
    font-size: 0.8em;
    padding: $spacing-unit * 1/2;
    width: 100%;

    & + .dropzone {
      border-radius: 0 0 4px 4px;
      border-top: none;
    }

    &__file {
      text-align: left;
      white-space: nowrap;

      .icon {
        display: inline-block;
        fill: $dropzone--file--icon--fill;
        height: 12px;
        margin-right: 4px;
        vertical-align: baseline;
        width: 12px;
      }

      .icon--close {
        height: 8px;
        margin-left: 4px;
        margin-right: 0;
        vertical-align: middle;
        width: 8px;
      }
    }
  }

  &__file {
    display: flex;
    width: 100%;

    &__item {
      flex: 1 0 auto;

      &--icon {
        flex: 0 0 auto;
      }

      &--file-name {
        flex: 1 1 auto;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      &--remove-icon {
        cursor: pointer;
      }
    }

    .icon {

    }
  }
}
