.textbox-repeater {

  .floating-action {

    &__group {
      margin-top: -8px;
      position: absolute;
      right: -8px;
      top: 50%;
    }

    &__button {
      position: relative;
    }
  }

  .textbox {

    &__wrapper {
      position: relative;
    }
  }

  .form {

    &__row {

      & + .form__row {
        margin-top: $spacing-unit * 1/2;
      }
    }
  }
}
